import L from 'leaflet';

// private private
// * 
// * getOwnPropertySymbols
const map = Symbol('map');
const init = Symbol('init');
const zoom = Symbol('zoom');

// Symbol
import "babel-polyfill";

/**
 * 
 * 
 * @export
 * @class Map
 */
export default class Map {

  /**
   * Creates an instance of Map.
   * 
   * @memberOf Map
   */
  constructor() {
    this[map] = {};
    this[zoom] = 16;
    this[init]();
  }

  /**
   * 
   * Leaflet.jsLayer
   * 
   * @returns 
   * 
   * @memberOf Map
   */
  [init]() {
    // map
    if (!document.getElementById("map")) {
      console.log("this page is not map page");
      return;
    }

    let std = L.tileLayer('http://{s}.tile.osm.org/{z}/{x}/{y}.png', {
      attribution: '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
    });

    let chiriin = L.tileLayer('https://cyberjapandata.gsi.go.jp/xyz/std/{z}/{x}/{y}.png', {
      attribution: "<a href='http://portal.cyberjapan.jp/help/termsofuse.html' target='_blank'></a>"
    });


    let pf = new L.tileLayer('http://tile.openweathermap.org/map/precipitation_new/{z}/{x}/{y}.png?appid=9f70e15ce517f3eb8a5c50dabd8eaf57', {
      attribution: '<a href="">precipitation</a>',
      // opacity: 0.8
    });

    let pr = new L.tileLayer('http://tile.openweathermap.org/map/pressure_new/{z}/{x}/{y}.png?appid=9f70e15ce517f3eb8a5c50dabd8eaf57', {
      attribution: '<a href="">pressure</a>',
      // opacity: 0.8
    });


    let wi = new L.tileLayer('http://tile.openweathermap.org/map/wind_new/{z}/{x}/{y}.png?appid=9f70e15ce517f3eb8a5c50dabd8eaf57', {
      attribution: '<a href="">wind</a>',
      // opacity: 0.8
    });

    let te = new L.tileLayer('http://tile.openweathermap.org/map/temp_new/{z}/{x}/{y}.png?appid=9f70e15ce517f3eb8a5c50dabd8eaf57', {
      attribution: '<a href="">tempressure</a>',
      // opacity: 0.8
    });

    this[map] = L.map("map", {
      center: [37.09, 138.52],
      zoom: this[zoom],
      layers: [std]
    });

    // 
    let baseMaps = {
      "Mapbox(osm)": std,
      "Mapbox(chiriin)": chiriin
    };

    // 
    let overlayMaps = {
      'Precipitation': pf,
      'Pressure': pr,
      'Wind': wi,
      'Temperature': te
    };

    L.control.layers(baseMaps, overlayMaps).addTo(this[map]);

    // 
    L.control.scale().addTo(this[map]);
    
    // 
    this[map].on('resize', () => {
      this[map].invalidateSize();
    });
  }

  /**
   * 
   * 
   * 
   * @param {any} lat 
   * @param {any} lon 
   * 
   * @memberOf Map
   */
  mapPan(lat, lon) {
    this[map].panTo([lat, lon]);
    this[map].setZoom(this[zoom]);
  }
}


